@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    overflow: hidden;
    background-color: #333;
    color: white;
  }
}

@layer components {
  .traffic-light {
    @apply w-3 h-3 rounded-full border border-gray-300 cursor-pointer transition-transform duration-100;
  }
  
  .traffic-light:hover {
    @apply scale-110;
  }
  
  .traffic-light.red {
    @apply bg-red-500 border-red-400;
  }
  
  .traffic-light.yellow {
    @apply bg-yellow-400 border-yellow-300;
  }
  
  .traffic-light.green {
    @apply bg-green-500 border-green-400;
  }
  
  .window-shadow {
    box-shadow: 0 8px 40px 8px rgba(0,0,0,0.18), 0 1.5px 8px rgba(0,0,0,0.10);
  }
  
  .window-shadow-dark {
    box-shadow: 0 4px 32px rgba(0,0,0,0.30);
  }
  
  .dock-icon-hover {
    @apply transform scale-115 -translate-y-1.5;
  }
  
  .calendar-day {
    @apply w-10 h-10 inline-flex items-center justify-center rounded-full cursor-pointer transition-all duration-200 font-medium text-gray-800 border-2 border-transparent mx-auto relative text-base;
  }
  
  .calendar-day:hover {
    @apply bg-blue-50 border-blue-500 text-blue-600 shadow-lg;
  }
  
  .calendar-day.today {
    @apply bg-blue-500 text-white font-bold border-blue-500 shadow-lg;
  }
  
  .skill-icon {
    @apply flex flex-col items-center w-16 relative cursor-pointer;
  }
  
  .skill-icon img {
    @apply w-8 h-8 transition-transform duration-150;
  }
  
  .skill-icon:hover img {
    @apply scale-110;
  }
  
  .skill-label {
    @apply mt-1.5 text-sm text-gray-600 font-medium text-center;
  }

  .mac-window {
    @apply bg-black bg-opacity-80 border border-white border-opacity-10 rounded-2xl shadow-2xl w-full max-w-xl text-green-200;
  }

  .mac-terminal {
    @apply flex flex-col gap-4 p-5;
  }

  .mac-window-header {
    @apply flex items-center justify-between text-xs text-gray-300 mb-2;
  }

  .mac-window-dots {
    @apply flex items-center gap-1;
  }

  .mac-window-dot {
    @apply w-3 h-3 rounded-full inline-block;
  }

  .dot-red {
    @apply bg-red-500;
  }

  .dot-yellow {
    @apply bg-yellow-400;
  }

  .dot-green {
    @apply bg-green-500;
  }

  .mac-window-title {
    @apply tracking-wide uppercase text-[11px];
  }

  .terminal-body {
    @apply font-mono text-sm leading-relaxed min-h-[180px];
  }

  .terminal-cta {
    @apply flex flex-wrap gap-3;
  }

  .terminal-button {
    @apply px-4 py-2 rounded-lg bg-white text-gray-900 text-sm font-semibold shadow;
  }

  .terminal-button.secondary {
    @apply bg-transparent border border-white text-white;
  }

  .hero-workspace {
    @apply w-full flex flex-col gap-8 lg:flex-row max-w-5xl;
  }

  .hero-main {
    @apply flex-1 flex flex-col gap-6;
  }

  .hero-side {
    @apply w-full lg:w-80 flex flex-col gap-6;
  }

  .stat-grid {
    @apply grid grid-cols-2 gap-4;
  }

  .stat-widget {
    @apply bg-white bg-opacity-20 rounded-2xl p-4 text-center text-white shadow-lg border border-white border-opacity-10;
  }

  .stat-icon {
    @apply text-2xl;
  }

  .stat-value {
    @apply text-xl font-semibold;
  }

  .stat-label {
    @apply text-xs uppercase tracking-wide text-gray-200;
  }

  .tech-grid {
    @apply grid grid-cols-4 gap-3 bg-white bg-opacity-10 rounded-2xl p-4;
  }

  .tech-icon {
    @apply flex flex-col items-center text-center gap-1 text-white;
  }

  .tech-emoji {
    @apply text-xl;
  }

  .tech-label {
    @apply text-xs;
  }

  .sticky-grid {
    @apply flex flex-wrap gap-4;
  }

  .sticky-note {
    @apply w-40 min-h-[160px] rounded-lg shadow-xl p-3 flex flex-col gap-2 rotate-[-2deg];
  }

  .sticky-note h3 {
    @apply text-base font-semibold;
  }

  .sticky-note p {
    @apply text-sm;
  }

  .sticky-tech {
    @apply flex flex-wrap gap-1 text-xs font-semibold;
  }

  .sticky-tech span {
    @apply bg-black bg-opacity-10 px-2 py-0.5 rounded-full;
  }

  .sticky-links {
    @apply flex justify-between text-sm font-semibold underline;
  }
}
